@import url(/node_modules/antd/dist/antd.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: lightgray;
}

#user-interface{
  display: grid;
  grid-template-columns: repeat(100, 1fr);
  grid-template-rows: repeat(100, 1fr);
  height: 100vh;
  grid-gap: 3px;

}
#nav-log-top-left{
    grid-row: 1;
    grid-column: 1;
    font-size: 30px;
    text-align: center;


}

#nav-bar{
    grid-column:1/8 ;
    grid-row:1/102 ;
    background: lightgray;
    width: 60px;
    min-width: 60px;
    display: grid;
    grid-template-rows: repeat(12, 1fr) ;
    
  }

  .slider{
      grid-row: 10/12 ;
      grid-column:1 ;
      align-self: center;
      justify-self: center ;
    /* height: 10px; */
    }
    
.hard{
    grid-row: 12 ;
    grid-column:1 ;
    justify-self: center ;
    /* align-self: center; */
    margin: 8px;

  }

.easy{
    grid-row: 9 ;
    grid-column:1 ;
    justify-self: center ;
    align-self: flex-end;
    margin: 10px;

  }

  
  #article-title{
    grid-column:30/80 ;
    grid-row:6/16 ;
    background: lightgray;
    /* border: 1px solid black; */
    text-align: center;
    
    
  }
  

#vocabulary-box{
    grid-column: 10/25 ;
    grid-row: 70/102;
    border: solid 1px gray;
    text-align: center;
    padding: 5px;
  
  }
  .quiz-box{
    grid-column: 83/98;
    grid-row:2/18 ;
  }
  .statistics-box{
    grid-column: 83/98 ;
    grid-row: 70/102;
  }

#article-content {
    width: inherit;
    padding: 0px 5px;
    grid-area: 2/3;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: space-between;
    
    border: lightslategray 1px solid;
    grid-column:30/80 ;
    grid-row:17/102;
    padding: 5px;
  }

.word-ul {
    list-style-type: none;
    padding: 2px 4px;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    margin: 0.3%; 
  }
  
  .word {
    order: 0;
    font-size: 20px;
  }
  
  .translatedWord{
    order: 1;
    color:#676161;
    font-size: 11px;
  
  }
#article-link-box{
    grid-column:30/80 ;
    grid-row:2/2 ;
    color: purple;
  
  }


  /* author box */
  #article-author-box{
    grid-column:30/80 ;
    grid-row:12/16 ;
    background: lightgray;
    display: grid;
    grid-gap: 2px;
    grid-template-columns: 1fr 6fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
  
  .article-author-pic{
    align-self: center;
    grid-column: 1;
    grid-row: 1/4;
    border: 1px solid;
  
  
  }
  .article-author-line {
    grid-column:2/7;
    grid-row: 1 ;
    font-size: xx-small;
  }
  .article-author-read-time{
    grid-column:2/7;
    grid-row: 3;
    font-size: xx-small;
  
  }
  
  
